<template>
  <div class="map-container">
    <!-- 加载地图文件按钮 -->
    <button @click="loadLocalFile">Load Local File</button>

    <!-- 地图渲染 -->
    <div v-if="grid.length" class="map">
      <div v-for="(row, rowIndex) in grid" :key="rowIndex" class="row">
        <div
          v-for="(cell, cellIndex) in row"
          :key="cellIndex"
          :class="['cell', cell === 1 ? 'black' : 'white']"
        ></div>
      </div>

      <!-- 汽车动态渲染 -->
      <img
        v-for="(car, index) in cars"
        :key="index"
        :src="car.image"
        :alt="`Car ${index + 1}`"
        class="car"
        :style="{
          top: `${car.position.row * cellSize}px`,
          left: `${car.position.col * cellSize}px`,
          transform: `rotate(${car.angle}deg)`
        }"
      />
    </div>
  </div>
</template>
<!-- {
  "carIndex": 0,
  "row": 5,
  "col": 7,
  "angle": 90
} -->

<script>
export default {
  name: "MapWithCars",
  data() {
    return {
      grid: [], // 用于存储从文本文件解析出的二维数组
      cars: [
        { image: require("@/assets/red.png"), position: { row: 0, col: 0 }, angle: 0 },
        { image: require("@/assets/yellow.png"), position: { row: 0, col: 0 }, angle: 0 },
        { image: require("@/assets/green.png"), position: { row: 0, col: 0 }, angle: 0 },
        { image: require("@/assets/gray.png"), position: { row: 0, col: 0 }, angle: 0 },
        { image: require("@/assets/blue.png"), position: { row: 0, col: 0 }, angle: 0 },
        { image: require("@/assets/orange.png"), position: { row: 0, col: 0 }, angle: 0 },
      ],
      cellSize: 1.25, // 每个网格单元的大小（像素）
      socket: null, // WebSocket 连接
    };
  },
  methods: {
    async loadLocalFile() {
      try {
        const response = await fetch("/grid_output.txt");
        const content = await response.text();
        this.processFileContent(content);
      } catch (error) {
        console.error("Failed to load file:", error);
      }
    },
    processFileContent(content) {
      const rows = content.trim().split("\n");
      const grid = rows.map((row) =>
        row.trim().split(/\s+/).map((value) => parseInt(value, 10))
      );

      this.grid = grid;
      this.placeCarsInValidPositions();
    },
    placeCarsInValidPositions() {
      const validPositions = [];

      // 收集所有黑色网格的位置
      this.grid.forEach((row, rowIndex) => {
        row.forEach((cell, colIndex) => {
          if (cell === 1) {
            validPositions.push({ row: rowIndex, col: colIndex });
          }
        });
      });

      // 随机选择黑色网格位置，确保位置唯一
      const usedPositions = new Set();
      this.cars.forEach((car) => {
        let randomPosition;
        do {
          const randomIndex = Math.floor(Math.random() * validPositions.length);
          randomPosition = validPositions[randomIndex];
        } while (usedPositions.has(`${randomPosition.row}-${randomPosition.col}`));

        usedPositions.add(`${randomPosition.row}-${randomPosition.col}`);
        car.position = randomPosition;
      });
    },
    connectSocket() {
      this.socket = new WebSocket("ws://localhost:8080");

      this.socket.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);

          // 更新车辆位置和角度
          if (data.carIndex !== undefined && this.cars[data.carIndex]) {
            const car = this.cars[data.carIndex];
            const { row, col, angle } = data;

            // 确保目标位置在黑色网格内
            if (this.grid[row]?.[col] === 1) {
              car.position.row = row;
              car.position.col = col;
              car.angle = angle || 0;
            } else {
              console.warn(`Invalid position for car ${data.carIndex}: (${row}, ${col})`);
            }
          }
        } catch (error) {
          console.error("Failed to parse socket message:", error);
        }
      };

      this.socket.onclose = () => {
        console.warn("Socket connection closed. Reconnecting...");
        setTimeout(this.connectSocket, 1000); // 重连
      };
    },
  },
  mounted() {
    this.loadLocalFile();
    this.connectSocket();
  },
};
</script>

<style scoped>
/* 地图容器样式 */
.map-container {
  position: relative;
  display: inline-block;
}

/* 地图样式 */
.map {
  position: relative;
  display: inline-block;
}

/* 地图网格行 */
.row {
  display: flex;
}

/* 单元格样式 */
.cell {
  width: 1.25px; /* 单元格宽度 */
  height: 1.25px; /* 单元格高度 */
}
.cell.white {
  background-color: white;
}
.cell.black {
  background-color: black;
}

/* 汽车图片样式 */
.car {
  position: absolute;
  width: 20px; /* 汽车图片宽度，比例缩小 */
  height: 30px; /* 汽车图片高度 */
  transition: top 0.3s ease, left 0.3s ease, transform 0.3s ease; /* 动画过渡效果 */
}
</style>
